import { Carousel } from 'react-responsive-carousel';
import { Section } from 'react-scroll-section';

export default function EmployerCarousel() {
  return (
    <Section id="home">
      <Carousel
        showArrows
        showThumbs={false}
        autoPlay
        interval={5000}
        infiniteLoop
        stopOnHover
        swipeable
        emulateTouch
        thumbWidth={100}
        showStatus={false}
        className="mb-5 lg:min-h-[500px]"
      >
        <div>
          <img src="/img/banners/banner-1.png" alt="banner-1" />
        </div>
        <div>
          <img src="/img/banners/banner-1.png" alt="banner-1" />
        </div>
      </Carousel>
    </Section>
  );
}
